@import "../../../../assets/styles/global";

.wrapper {
  padding-top: 48px;
  display: inline-flex;
  flex-direction: column;

  .image {
    margin-bottom: 32px;
    position: relative;
    width: max-content;

    .ﾑ（rcle {
      width: 368px;

      img {
        width: 100%;
      }
    }

    .photo {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-46%, -50%);
      width: 280px;
      height: 280px;

      img {
        width: 100%;
        border-radius: 50%;
      }
    }
  }

  .text {
    width: 368px;
    height: 360px;
    padding: 16px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    border: 4px solid #72A6D6;
    box-shadow: 0 0 11px rgba(114, 166, 214, 0.65);
    border-radius: 4px;
    background: rgba(114, 166, 214, 0.12);
    overflow: hidden;

    .name {
      font-family: $Gotham;
      font-style: normal;
      font-weight: bold;
      font-size: 24px;
      line-height: 29px;
      text-transform: uppercase;
      color: #FFFFFF;
    }

    .description {
      padding-top: 16px;
      font-family: $Nunito;
      font-style: normal;
      font-weight: 300;
      font-size: 18px;
      line-height: 25px;
      color: #FFFFFF;

      span {
        color: #72A6D6;
        text-transform: uppercase;
        font-weight: bold;
      }
    }
  }
}

.popup {
  display: none;
}

@media screen and (max-width: $Tablet) {
  .wrapper {
    padding-top: (48px/$val);

    .image {
      margin-bottom: (32px/$val);

      .ﾑ（rcle {
        width: (368px/$val);
      }

      .photo {
        width: (280px/$val);
        height: (280px/$val);
      }
    }

    .text {
      width: (368px/$val);
      height: (360px/$val);
      padding: (16px/$val);
      border: (4px/$val) solid #72A6D6;

      .name {
        font-size: (24px/$val);
        line-height: (29px/$val);
      }

      .description {
        padding-top: (16px/$val);
        font-size: (18px/$val);
        line-height: (25px/$val);
      }
    }
  }
}

@media screen and (max-width: $Mobile) {
  .wrapper {
    padding-top: 0;
    margin: 32px 8px 0 8px;

    .image {
      margin-bottom: 8px;

      .ﾑ（rcle {
        width: 94px;
      }

      .photo {
        transform: translate(-46%, -50%);
        width: 66px;
        height: 66px;
      }
    }

    .text {
      width: auto;
      max-width: 100px;
      height: auto;
      padding: 0;
      border: none;
      align-items: center;
      box-shadow: none;
      border-radius: 0;
      background: none;

      .name {
        text-align: center;
        font-size: 14px;
        line-height: 17px;
      }

      .description {
        display: none;
      }
    }
  }

  .popup {
    display: block;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.85);
    z-index: 99;
    visibility: hidden;
    opacity: 0;
    transition: .3s;

    .content {
      display: inline-flex;
      flex-direction: column;
      align-items: center;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      opacity: 0;
      transition: .3s;
      z-index: 100;

      .image {
        margin-bottom: 16px;
        position: relative;
        width: max-content;

        .ﾑ（rcle {
          width: 220px;

          img {
            width: 100%;
          }
        }

        .photo {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-46%, -50%);
          width: 159px;
          height: 159px;

          img {
            width: 100%;
            border-radius: 50%;
          }
        }
      }

      .text {
        width: 285px;
        padding: 16px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        border: 2px solid #72A6D6;
        box-shadow: 0 0 11px rgba(114, 166, 214, 0.65);
        border-radius: 4px;
        background: rgba(114, 166, 214, 0.12);
        overflow: hidden;

        .name {
          font-family: $Gotham;
          font-style: normal;
          font-weight: bold;
          font-size: 19px;
          line-height: 19px;
          text-transform: uppercase;
          color: #FFFFFF;
        }

        .description {
          padding-top: 8px;
          font-family: $Nunito;
          font-style: normal;
          font-weight: 300;
          font-size: 11px;
          line-height: 15px;
          color: #FFFFFF;

          span {
            color: #72A6D6;
            text-transform: uppercase;
            font-weight: bold;
          }
        }
      }
    }
  }

  .popup.show {
    visibility: visible;
    opacity: 1;

    .content {
      opacity: 1;
    }
  }
}
